.footer-section--container {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 20px;
  background-color: #2d2e55;
  display: flex;
  color: #f5f4f5;
}

.footer-left--container {
  width: 33.33%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-left--container h1 {
  margin: 0;
  color: #d1d2d6;
  font-size: 3rem;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.footer-left--container p {
  font-size: 1rem;
  text-align: center;
  margin-top: -10px;
  color: #d1d2d6;
}

.footer-middle--container {
  box-sizing: border-box;
  padding-left: 50px;
}

.footer-middle--container,
.footer-right--container {
  width: 33.33%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-middle--container h2,
.footer-right--container h2 {
  margin: 0;
}

.footer-middle--container ul {
  list-style-type: none;
  margin: 0;
  width: 50%;
}

.footer-middle--container ul li {
  margin: 10px 0;
  transition: all 0.5s ease-out;
  display: flex;
}
.footer-middle--container ul li:hover {
  margin-left: 10px;
}

.footer-middle--container ul li a {
  color: #f5f4f5;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-main--container {
  display: flex;
  flex-wrap: wrap;
}

.icon-container {
  border: 3px solid #f5f4f5;
  color: #f5f4f5;
  padding: 0px;
  width: 48px;
  height: 48px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.5s ease-out;
  margin: 15px 8px 10px 8px;
}

.footer--border {
  height: 4px;
  width: 250px;
  background-color: #f5f4f5;
  margin: -10px 0 15px 0;
  border-radius: 500px;
}

.icon-container:hover {
  border-radius: 0 10px 0 10px;
  transform: rotate(360deg);
}

.footer-eater {
  margin-top: -15px !important;
}

.icon-container-mail {
  color: #f5f4f5;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mid-line-footer {
  position: relative;
  height: 4px;
  border-radius: 500px;
  width: 200px;
  background-color: #f5f4f5;
  margin: 15px 0 20px 0;
}

.mid-line-footer p {
  position: absolute;
  top: -25px;
  left: 45%;
  height: 20px;
  width: 30px;
  text-align: center;
  letter-spacing: 1px;
  background-color: #2d2e55;
}

@media only screen and (max-width: 800px) {
  .footer-section--container {
    flex-direction: column;
    padding: 20px;
    width: 89%;
  }

  .footer-left--container,
  .footer-middle--container,
  .footer-right--container {
    width: 100%;
    height: 100%;
    display: flex;
    padding: 10px 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .footer-middle--container ul {
    width: 90%;
    padding: 0;
  }

  .footer-middle--container ul li {
    margin: 10px 0;
    margin-left: 38%;
  }
}

.copyright-issue {
  background-color: rgba(45, 46, 85, 0.9);
  color: #f5f4f5;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
  width: 100%;
  padding: 10px 20px;
}

.icon-main--container p {
  font-size: 12px;
  padding: 0 80px;
  text-align: center;
  margin: 0;
}

@media only screen and (max-width: 450px) {
  .footer-section--container {
    width: 100%;
  }

  .footer-middle--container ul li {
    margin: 10px 0;
    margin-left: 22%;
  }

  .footer-middle--container h2,
  .footer-right--container h2 {
    font-size: 1.8rem;
  }

  .footer-middle--container ul li a {
    font-size: 20px;
  }
}
