.expore-main--container {
  background-color: #2d2e55;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 80px 0 100px 0;
  position: relative;
}

.explore-card-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 100px 0 0 0;
}
@media only screen and (max-width: 450px) {
  .explore-card-container {
    width: 98%;
  }
  .background-explore {
    height: 50%;
  }
}
