.about-section--container {
  background-color: #f5f4f5;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 60px;
}

.main-heading--container {
  margin-top: 30px;
}
.about-main--container {
  display: flex;
  padding: 10px 60px 80px 60px;
  justify-content: space-between;
  align-items: center;
}
.about-image {
  width: 280px;
  transition: all 1s ease-out;
}

.about-image--container {
  width: 330px;
  height: 230px;
  border: 10px solid #2d2e55;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-image--container:hover > .about-image {
  transform: scale(0.1) rotate(1080deg);
}

.about-content--container {
  width: 65%;
  text-align: justify;
  font-size: 1.5rem;
}

.code-eater--about {
  font-weight: 600;
  font-size: 3rem;
  color: #2d2e55;
}

.about-icon--container {
  display: flex;
}

.about-icon-container {
  background-color: #2d2e55;
  margin: 0 10px;
  cursor: pointer;
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 500px;
}

.about--discord {
  margin-left: 0px;
}

.about--discord:hover {
  background-color: #738adb;
}

.about--insta:hover {
  background-color: #cd486b;
}

.about--youtube:hover {
  background-color: #ff0000;
}

@media only screen and (max-width: 800px) {
  .main-heading--container {
    margin: 25px;
  }

  .about-main--container {
    width: 85%;
    padding: 10px 0px 80px 0px;
    flex-direction: column;
  }
  .about-image--container {
    margin: 20px 0;
  }

  .about-content--container {
    width: 100%;
    margin: 0;
    font-size: 1.5rem;
  }
}

@media (max-width: 450px) {
  .code-eater--about {
    font-weight: 600;
    font-size: 3.2rem;
    color: #2d2e55;
  }
}

@media (min-width: 411px) and (max-width: 415px) {
  .about-content--container {
    width: 90%;
  }
}

@media (min-width: 392px) and (max-width: 394px) {
  .about-content--container {
    width: 96%;
  }
}

@media (min-width: 359px) and (max-width: 361px) {
  .about-content--container {
    font-size: 1.4rem;
  }
}
