.carousel-root {
  /* margin-top: 60px; */
  height: 100vh !important;
  overflow: hidden;
}

@media (max-width: 900px) {
  .carousel-root {
    margin-top: 60px;
    height: 75vh !important;
  }
}

@media (max-width: 600px) {
  .carousel-root {
    margin-top: 60px;
    height: 25vh !important;
  }
}

@media (max-width: 450px) {
  .carousel-root {
    margin-top: 60px;
    height: 10vh !important;
  }
}
