.course-section-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #2d2e55;
  position: relative;
  padding: 50px 0;
}

.course-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 50px 100px;
}

@media only screen and (max-width: 450px) {
  .half-background {
    height: 53%;
  }
}
