.blogFront-main-container {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  margin: 30px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.blogfront-top--container {
  width: 250px;
  height: 350px;
  border-radius: 10px;
}

.blog-front--img {
  transform: translate(0);
  width: 250px;
  height: 310px;
  border-radius: 0;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  filter: grayscale(0%);
}

.blog-front--title {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
  letter-spacing: 0px;
  margin: 0px 0 8px 0;
  color: #2d2e55;
}

.blogfront-back--container {
  box-sizing: border-box;
  width: 250px;
  height: 350px;
  background-color: #f5f4f5;
  position: absolute;
  top: 0;
  border-radius: 10px;
  transform: translateX(-250px);
  transition: all 0.3s ease-out;
  padding: 6px;
}

.blog-back--post {
  font-size: 0.8rem;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  margin: 0px 0 0 0;
  color: #2d2e55;
}

.blogFront-main-container:hover > .blogfront-back--container {
  transform: translateX(0px);
}

.blogfront-back--descrip {
  font-size: 14px;
  padding: 0 10px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
  text-align: justify;
  margin: 6px 0 10px 0;
}

.blog-content-front {
  margin: 0;
}

.blogfront-blog--button {
  color: #e7e7e7;
  background-color: #a7b0b2;
  font-size: 12px;
  font-weight: 700;
  padding: 10px 15px;
  border: none;
  border-radius: 50px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 0 auto;
  width: 100px;
  height: 15px;
  text-decoration: none;
  background-color: #a7b0b2;
}

.blogfront-blog--button:hover {
  background-color: #ed4933;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width: 800px) {
  .blog-utility--content {
    width: 100% !important;
  }
}
