.navigation {
  width: 100%;
  animation: navAnim 0.5s ease-out;
  opacity: 0;
  animation-delay: 4.5s;
  animation-fill-mode: forwards;
  position: fixed;
  top: -10px;
  z-index: 50;
  margin: 0;
  margin-top: 10px;
  color: #2d2e55;
}

.navigation ul {
  list-style-type: none;
  display: flex;
  margin: 0;
  padding: 25px 10px;
  background-color: #2e2d55;
  justify-content: space-evenly;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.navigation ul li {
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #f5f4f5;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 4px solid transparent;
  transition: all 0.3s ease-out;
}

.navigation ul li:hover {
  border-left: 4px solid red;
}

@keyframes navAnim {
  0% {
    transform: translateY(-80px);
  }
  80% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
    opacity: 1;
  }
}

.burger-navigation {
  width: 97%;
  height: 80px;
  color: white;
  background-color: #2e2d55;
  cursor: pointer;
  display: none;
  pointer-events: visible;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.code-eater-nav-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.code-eater-nav-logo > * {
  width: 45px;
}

@media only screen and (max-width: 800px) {
  .navigation ul {
    flex-direction: column;
    transform: translateY(-550px);
    pointer-events: visible;
  }
  .navigation ul li {
    margin: 10px 44%;
    font-size: 1.7rem;
    justify-content: flex-start;
  }

  .burger-navigation {
    display: flex;
    align-items: center;
    padding: 0 10px;
    justify-content: space-between;
    z-index: 1000;
  }
}

@media only screen and (max-width: 450px) {
  .navigation {
    width: 98%;
    pointer-events: none;
  }
  .navigation ul {
    width: 100%;
  }

  .navigation ul li {
    margin: 10px 25%;
  }
}
