.main-course--container {
  width: 260px;
  height: 340px;
  background-color: #f7f7f7;
  color: #2e3842;
  border-radius: 10px;
  transition: all 0.4s ease-out;
  margin: 20px 30px;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.main-course--container:hover > .card-image {
  transform: translate(0px);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.card-image {
  border-radius: 10px;
  width: 260px;
  height: 280px;
  transition: all 0.3s ease-out;
  transform: translate(-10px, -10px);
}

.course-card--content {
  padding: 0 10px 10px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.course-button-container {
  display: flex;
  justify-content: space-between;
}

.course-button-container > * {
  background-color: #a7b0b2;
  width: 100%;
  color: #ffffff;
  border: none;
  font-weight: 600;
  font-size: 13px;
  text-decoration: none;
  padding: 12px;
  text-transform: uppercase;
  letter-spacing: 1px;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.course-button-container > *:hover {
  background-color: #ed4933;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.share-button {
  width: 45px;
}

.share-button img {
  width: 15px;
}

/* -----------modal------------------ */
.modal {
  position: fixed;
  z-index: 6 !important;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #2e2d55;
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  padding: 20px;
  border: 1px solid #888;
  height: 75vh;
  width: 60%;
  margin-top: 50px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.form-container-step2 {
  display: flex;
  flex-direction: column;
}

.form-step2-div1 {
  display: flex;
}

.form-step2-div1 h2 {
  width: 50%;
  text-align: center;
  color: #2d2e55;
}

.form-step2-div1 h3 {
  width: 50%;
  color: #2d2e55;
  text-transform: uppercase;
  text-align: center;
}

.form-container--modal {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.form-input--select {
  width: 323px !important;
}

.form-input-modal {
  font-size: 15px;
  padding: 10px;
  width: 300px;
  margin: 10px;
  border: 1.5px solid #2d2e55;
  border-radius: 4px;
  color: rgba(45, 46, 85, 0.8);
}
.form-input-modal:focus {
  border: 1.5px solid yellowgreen;
}

.form-input-modal::placeholder {
  color: rgba(45, 46, 85, 0.8);
}

.form-input-modal:focus {
  outline: none;
}

.modal-form--button {
  margin: 10px 0;
  font-size: 15px;
  padding: 15px 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 500;
  width: 92%;
  cursor: pointer;
  border-radius: 500px;
  border: none;
  font-weight: bold;
  letter-spacing: 2px;
  background-color: #2d2e55;
  color: white;
}

.please-wait--container {
  display: flex;
  align-items: center;
  justify-content: center;
}
.please-wait {
  text-align: center;
  font-size: 3rem;
}

@media (max-width: 900px) {
  .modal-content {
    background-color: #fefefe;
    padding: 20px;
    border: 1px solid #888;
    height: auto;
    width: 100%;
  }
}

@media (max-width: 550px) {
  .modal {
    overflow: scroll;
  }
  .modal-content {
    background-color: #fefefe;
    padding: 120px 20px 20px 20px;
    border: 1px solid #888;
    height: auto;
    width: 100%;
    overflow: scroll !important;
  }
  .form-step2-div1 {
    display: flex;
    flex-direction: column;
  }

  .form-step2-div1 h2 {
    width: 100%;
  }

  .form-step2-div1 h3 {
    width: 100%;
    margin-top: 0;
  }
}
